import { Component, Inject, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { CompensationReasons } from 'src/app/shared/Models/Models';
import { AddCompensationService } from './add-compensation.service';

@Component({
  selector: 'app-add-compensation',
  templateUrl: './add-compensation.component.html',
  styleUrls: ['./add-compensation.component.scss']
})
export class AddCompensationComponent implements OnInit {

  reasons = [] as any;
  selectedReason: any = null;

  constructor(@Inject(MAT_DIALOG_DATA) public data: any, public matDialogRef: MatDialogRef<AddCompensationComponent>,
  private _compensationService: AddCompensationService) { }

  compensationForm: FormGroup = new FormGroup({
    amount: new FormControl(null, [Validators.required, Validators.max(100.00), Validators.min(1.00)]),
    reason: new FormControl(this.selectedReason, Validators.required),
    explanation: new FormControl("", Validators.required)
  });

  ngOnInit(): void {
    if (this.data && this.data.reasons) {
      this.data.reasons.forEach((item: CompensationReasons) => {
        this.reasons.push({
          value: item.reason_code,
          displayValue: item.reason_code_name
        });
      });
    }
  }

  // reasonChanged() {
  //   if (this.compensationForm.controls.reason.value == "Other") {
  //     this.compensationForm.controls['explanation'].setValidators(Validators.compose([Validators.required, Validators.minLength(5)]));
  //   }
  //   else {
  //     this.compensationForm.controls['explanation'].setValidators(null);
  //   }
  //   this.compensationForm.controls['explanation'].updateValueAndValidity();
  // }

  onClickClose() {
    this.matDialogRef.close();
  }

  onSubmitClick() {
    const clientAcctId = localStorage.getItem("client_acct_id");
    const formValues = this.compensationForm.value;
    var reqObj = {
      amount: formValues.amount,
      master_plan_instance_no: this.data.masterPlanId,
      client_acct_id: clientAcctId,
      comments: formValues.explanation,
      reason_code: formValues.reason
    }
    if (this.compensationForm.valid) {
      this._compensationService.AddCompensation(reqObj).subscribe((result)=>{
        console.log(result);
        if(result && result.error_code===0 && result.error_msg === "OK"){
          return this.matDialogRef.close(result);
        } else {
          return this.matDialogRef.close(result);
        }
      })
    } else {
      return ;
    } 
    
  }
}
export interface radioOptions {
  value: number;
  displayValue: string;
}
