import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { CancelDTO, CompensationReasons, StatusDTO } from 'src/app/shared/Models/Models';
import { StepCompletionStatusComponent } from 'src/app/shared/step-completion-status/step-completion-status.component';
import { NotificationPreferenceComponent } from '../../notification-preference/notification-preference.component';
import { EditContactDetailsComponent } from '../edit-contact-details/edit-contact-details.component';
import { ExtendExpiryDateComponent } from '../extend-expiry-date/extend-expiry-date.component';
import { ManageDiscountsComponent } from '../../subscription-and-discounts/manage-discounts/manage-discounts.component';
import { ResetPinComponent } from '../reset-pin/reset-pin.component';
import { CancelPopupComponent } from 'src/app/shared/Cancel Popup/cancel-popup/cancel-popup.component';
import { ExtendExpiryDateDetailsComponent } from '../extend-expiry-date-details/extend-expiry-date-details.component';
import { AddCompensationComponent } from '../add-compensation/add-compensation.component';
import { PackageDetailsService } from './package-details.service';
import { Account, PaymentMethodsInfo } from 'src/app/register/register.model';
import { AcctComments, AcctContracts, AcctPaymentDetail, AcctPlan } from 'src/app/shared/Models/Account';
import { Ca_State_prov, Us_State_prov, PlanStatus } from 'src/app/shared/Models/Constants';
import { PlanRateSchedules } from '../../subscription-and-discounts/change-subscription/change-subscription.model';
import { Router } from '@angular/router';
import { EditPaymentInfoComponent } from 'src/app/payment-registeration/edit-payment-info/edit-payment-info.component';

@Component({
    selector: 'app-package-details',
    templateUrl: './package-details.component.html',
    styleUrls: ['./package-details.component.scss']
})
export class PackageDetailsComponent implements OnInit {

    constructor(private dialog: MatDialog, private router: Router, private packageDetailService: PackageDetailsService) { }

    isAdmin: boolean = false;
    stepSuccessful: boolean = false;
    status: string = 'PIN request submitted';
    statusDetails: string = 'Reset PIN request has been successfully sent to emily.smith@gmail.com.';

    newComment: string = "";

    AccountDetails: Account;
    ContactAddress: string;
    AccountPlan: AcctPlan;
    TotalAccountPlans: [AcctPlan];
    AccountPlanStatus: string;
    AccountPaymentDetail: PaymentMethodsInfo;
    AccountComments: AcctComments[];
    AcctContracts: AcctContracts;

    AllowCancelSubscription: boolean = true; // this will ensure to allow cancel subscription and add new subscription

    clientAcctId: { client_acct_id: string } = {
        client_acct_id: ''
    };


    masterPlans = [] as any;
    masterPlanSelection: number; // TODO: rethink type
    currentSelectedPlan: AcctPlan;
    selectedBillingGroupNo: number;
    compensationReasons:CompensationReasons[];

    ngOnInit(): void {
        const clientAcctId = localStorage.getItem("client_acct_id");
        const isAdmin = localStorage.getItem("is_admin");
        this.isAdmin = isAdmin && isAdmin === "yes" ? true : false;
        this.clientAcctId.client_acct_id = clientAcctId!;
        // this.EnableCancelSubscription(); // we have decided to calculate respective info in front-end as we already have the data for calculating it
        // this.getAcctDetails();
        this.getAcctPlans();
        this.getAcctPaymentDetail();
        this.getAcctComments();
        this.getAcctContracts();

        this.subscribedUserInfo();
        this.getCompensationReasons();
    }

    getCompensationReasons() {
       this.packageDetailService.getCompensationReasons().subscribe((result)=>{
        if(result && result.error_msg === "OK" && result.error_code===0){
            this.compensationReasons = result.all_reason_codes;
        }
       });
    }


    EnableCancelSubscription() {
        this.packageDetailService.EnableCancelSubscription(this.clientAcctId).subscribe((result) => {
            console.log(result);
            if (result && result.allowCancelSubscription) {
                this.AllowCancelSubscription = true;
            } else {
                this.AllowCancelSubscription = false;
            }
        });
    }

    getAcctDetails() {
        this.packageDetailService.GetAcctDetails(this.clientAcctId).subscribe((result) => {
            if (result) {
                this.AccountDetails = result;
                
                const address = this.constructAddress(this.AccountDetails);
                this.ContactAddress = address;

                // for showing payment related detials
                this.evaluatePaymentInfo();

            }
        });
    }
    evaluatePaymentInfo() {
        if (this.AccountDetails.payment_methods_info && this.AccountDetails.payment_methods_info.length > 0) {
            const payment_method_info = this.AccountDetails.payment_methods_info.filter
                (x => x.billing_group_no === this.selectedBillingGroupNo);
            console.log(payment_method_info);
            if (payment_method_info && payment_method_info.length > 0) {
                this.AccountPaymentDetail = payment_method_info[0];
                this.AccountPaymentDetail.payMethodTypeName = this.AccountPaymentDetail.pay_method_type === "1" ? "Credit Card" : "";
                this.AccountPaymentDetail.cardExpiration = this.AccountPaymentDetail.cc_expire_mm + "/" + this.AccountPaymentDetail.cc_expire_yyyy;
                this.AccountPaymentDetail.fullBillingAddress = this.constructPaymentAddress(this.AccountPaymentDetail);
            }
        }
    }

    constructAddress(data: any) {
        let constructAddress = data.address1 ? data.address1 : '';
        constructAddress = data.address2 ? constructAddress + ' ' + data.address2 : constructAddress;
        constructAddress = data.city ? constructAddress + ' ' + data.city : constructAddress;
        let country = '';
        let stateProv = '';

        if (data.country_cd) {
            if (data.state_prov && data.country_cd === "US") {
                const result = Us_State_prov.filter((item) => {
                    return item.value === data.state_prov;
                });
                if (result && result.length > 0) {
                    stateProv = result[0].displayValue;
                }
                country = "United States";
            } else if (data.state_prov && data.country_cd === "CA") {
                const result = Ca_State_prov.filter((item) => {
                    return item.value === data.state_prov;
                });
                if (result && result.length > 0) {
                    stateProv = result[0].displayValue;
                }
                country = "Canada";
            } else if (data.state_prov && data.country_cd === "GB") {
                country = "United Kingdom";
            }

        }

        constructAddress = constructAddress + ' ' + stateProv;
        constructAddress = constructAddress + ' ' + country;
        constructAddress = data.postal_cd ? constructAddress + ' ' + data.postal_cd : constructAddress;

        return constructAddress;
    }

    constructPaymentAddress(data: any) {
        let constructAddress = data.bill_address1 ? data.bill_address1 : '';
        constructAddress = data.bill_address2 ? constructAddress + ' ' + data.bill_address2 : constructAddress;
        constructAddress = data.bill_city ? constructAddress + ' ' + data.bill_city : constructAddress;
        let country = '';
        let stateProv = '';

        if (data.bill_country) {
            if (data.bill_state_prov && data.bill_country === "US") {
                const result = Us_State_prov.filter((item) => {
                    return item.value === data.bill_state_prov;
                });
                if (result && result.length > 0) {
                    stateProv = result[0].displayValue;
                }
                country = "United States";
            } else if (data.bill_state_prov && data.bill_country === "CA") {
                const result = Ca_State_prov.filter((item) => {
                    return item.value === data.state_prov;
                });
                if (result && result.length > 0) {
                    stateProv = result[0].displayValue;
                }
                country = "Canada";
            } else if (data.bill_state_prov && data.country === "GB") {
                country = "United Kingdom";
            }

        }

        constructAddress = constructAddress + ' ' + stateProv;
        constructAddress = constructAddress + ' ' + country;
        // constructAddress = data.bill_postal_cd ? constructAddress + ' ' + data.bill_postal_cd : constructAddress;

        return constructAddress;
    }

    getAcctPlans() {
        this.packageDetailService.GetAcctPlans(this.clientAcctId).subscribe((result) => {
            if (result && result.error_code === 0 && result.error_msg === "OK" && result.all_acct_plans_m.length > 0) {

                if (result && result.all_acct_plans_m && result.all_acct_plans_m.length > 0) {

                    this.TotalAccountPlans = result.all_acct_plans_m; //setting total accout plans to use in future

                    for (const iterator of result.all_acct_plans_m) {
                        let obj = {} as any;
                        obj.displayValue = `${iterator.master_plan_instance_no} - ${iterator.plan_name} - ${iterator.rate_schedule_name}`;
                        obj.value = iterator.master_plan_instance_no;

                        this.masterPlans.push(obj);
                    }

                    this.currentSelectedPlan = this.TotalAccountPlans[0]; //setting default selected Plan
                    this.masterPlanSelection = this.currentSelectedPlan.master_plan_instance_no; // setting default select box value
                    this.selectedBillingGroupNo = this.currentSelectedPlan.billing_group_no;

                    const findAcctPlan = this.TotalAccountPlans.filter(x => x.master_plan_instance_no === this.masterPlanSelection);

                    if (findAcctPlan && findAcctPlan.length > 0) {
                        this.AccountPlan = findAcctPlan[0];
                        const planStatus = PlanStatus.filter(x => x.value === this.AccountPlan.plan_instance_status_cd);
                        this.AccountPlanStatus = planStatus[0].displayValue; // setting status of current plan

                        // allow to cancel current selected plan or not
                        if (this.AccountPlan.plan_instance_status_cd === -2 || this.AccountPlan.plan_instance_status_cd === -3) {
                            this.AllowCancelSubscription = false;
                        } else {
                            this.AllowCancelSubscription = true;
                        }
                    }

                }

            }

            this.getAcctDetails();

        });
    }

    onPlanChange(event: any) {
        const findAcctPlan = this.TotalAccountPlans.filter(x => x.master_plan_instance_no === this.masterPlanSelection);
        if (findAcctPlan && findAcctPlan.length > 0) {
            this.AccountPlan = findAcctPlan[0];
            this.currentSelectedPlan = findAcctPlan[0];
            this.selectedBillingGroupNo = this.currentSelectedPlan.billing_group_no;

            const planStatus = PlanStatus.filter(x => x.value === this.AccountPlan.plan_instance_status_cd);
            this.AccountPlanStatus = planStatus[0].displayValue; // setting status of current plan

            // allow to cancel current selected plan or not
            if (this.AccountPlan.plan_instance_status_cd === -2 || this.AccountPlan.plan_instance_status_cd === -3) {
                this.AllowCancelSubscription = false;
            } else {
                this.AllowCancelSubscription = true;
            }

            // for showing payment related detials
            this.evaluatePaymentInfo();
        }

    }

    getAcctPaymentDetail() {
        // this.packageDetailService.GetAcctPaymentDetail(this.clientAcctId).subscribe((result) => {
        //     if (result && result.error_code === 0 && result.error_msg === "OK" && result.account_payment_methods.length > 0) {
        //         this.AccountPaymentDetail = result.account_payment_methods[0];
        //         const date = new Date(this.AccountPaymentDetail.from_date);
        //         this.AccountPaymentDetail.from_date = date.toLocaleDateString();
        //     }
        // });
    }

    getAcctComments() {
        this.packageDetailService.GetAcctComments(this.clientAcctId).subscribe((result) => {
            if (result && result.error_code === 0 && result.error_msg === "OK" && result.acct_comments && result.acct_comments.length > 0) {
                this.AccountComments = result.acct_comments;

                for (const iterator of this.AccountComments) {
                    let date = new Date(iterator.comment_date_time);
                    let time = date.toLocaleTimeString('en-US');
                    let localDate = date.toLocaleDateString();

                    iterator.commentTime = time;
                    iterator.commentDate = localDate;
                }

                this.AccountComments.sort(function(a ,b){
                    return new Date(b.comment_date_time).getTime() - new Date(a.comment_date_time).getTime();
                  });
                
            }
        });
    }

    getAcctContracts() {
        this.packageDetailService.GetAcctContracts(this.clientAcctId).subscribe((result) => {
            if (result && result.error_code === 0 && result.error_msg === "OK" && result.all_acct_contracts_m.length > 0) {
                this.AcctContracts = result.all_acct_contracts_m[0];
            }
        });
    }

    subscribedUserInfo() {
        this.packageDetailService.getUserName().subscribe((res) => {
            if (res && this.AccountDetails) {
                this.AccountDetails.first_name = res.first_name;
                this.AccountDetails.last_name = res.last_name;
                this.AccountDetails.phone = res.phone;

                const address = this.constructAddress(res);
                this.ContactAddress = address;
            }
        });
    }

    addComment() {
        if (this.newComment && this.newComment.length > 0) {
            let reqObj = {
                comment: this.newComment,
                client_acct_id: this.clientAcctId.client_acct_id
            }
            this.packageDetailService.AddComment(reqObj).subscribe((result) => {
                if (result && result.error_code === 0 && result.error_msg === "OK") {

                    this.newComment = '';
                    this.getAcctComments();

                } else {

                    let statusData: StatusDTO = {
                        title: "Error",
                        details: result.error_msg,
                        stepSuccessful: false
                    }

                    let ref = this.dialog.open(StepCompletionStatusComponent,
                        { height: '48vh', width: '42vw', data: statusData });
                }
            });
        }
    }

    CancelSubscriptionClick() {

        let cancelData: CancelDTO = {
            title: 'Cancel Subscription',
            details: 'Your trial subscription of the telematics package will end immediately. Please choose a reason for your cancellation to complete the process.',
            reasons: ['No Longer Own Vehicle', 'Technical Issues', 'Too Expensive', 'Not Using It', 'Lack of Value in the Service', 'Other']
        }

        let ref = this.dialog.open(CancelPopupComponent,
            { height: '95vh', width: '40vw', data: cancelData })

        ref.afterClosed().subscribe(result => {
            if (result) {

                // it means user has confirmed to cancel the subcription
                const reqObj = {
                    client_acct_id: this.clientAcctId.client_acct_id,
                    existing_master_plan_instance_no: this.AccountPlan.master_plan_instance_no,
                    cancel_subscription_reason: result
                }

                this.packageDetailService.CancelSubscription(reqObj).subscribe((res) => {
                    if (res && res.errorCode === 0 && res.errorMessage === "OK") {
                        let statusData: StatusDTO = {
                            title: "Subscription Cancelled",
                            details: "The trial subscription of the telematics package has been successfully cancelled.",
                            stepSuccessful: true
                        }
                        let ref = this.dialog.open(StepCompletionStatusComponent,
                            { height: '48vh', width: '40vw', data: statusData });
                        ref.afterClosed().subscribe((res) => {
                            // as it is on same route so following commented line will not work
                            //this.router.navigate(['/CustomerPortal/manage-subscription']);
                            window.location.reload();
                        });

                    } else {
                        let statusData: StatusDTO = {
                            title: "Subscription Cancel Error Occured",
                            details: res.error_msg,
                            stepSuccessful: false
                        }
                        this.dialog.open(StepCompletionStatusComponent,
                            { height: '48vh', width: '40vw', data: statusData })
                    }
                });
            }
        })
    }
    EditPaymentInformation() {
        var reqData = {
            master_plan_id: this.masterPlanSelection,
            client_acct_id: this.clientAcctId.client_acct_id 

        }
        this.dialog.open(EditPaymentInfoComponent,
            { height: '90vh', width: '60vw', data: reqData })
    }
    EditContactInfoClick() {
        this.dialog.open(EditContactDetailsComponent,
            { height: '90vh', width: '60vw' })
    }

    ManageDiscountsClick() {
        this.dialog.open(ManageDiscountsComponent,
            { height: '96vh', width: '60vw' })
    }

    ExtendExpiryDateClick() {
        let ref = this.dialog.open(ExtendExpiryDateComponent,
            { height: '90vh', width: '45vw' })

        ref.afterClosed().subscribe(result => {
            if (result == true) {
                this.dialog.open(ExtendExpiryDateDetailsComponent,
                    { height: '80vh', width: '40vw' })
            }
        })
    }

    AddCompensationClick() {
       
        var shareData = {
            reasons:this.compensationReasons,
            masterPlanId:this.masterPlanSelection
        }
        let ref = this.dialog.open(AddCompensationComponent,
            { height: '80vh', width: '40vw',data:shareData })

        ref.afterClosed().subscribe(result => {
            if (result && result.error_code===0 && result.error_msg === "OK") {
                let statusData: StatusDTO = {
                    title: "Compensation Added",
                    details: "The credit has been applied to the account.",
                    stepSuccessful: true
                }
                this.dialog.open(StepCompletionStatusComponent,
                    { height: '45vh', width: '40vw', data: statusData })
            } else if(result && result.error_cod != 0) {
                let statusData: StatusDTO = {
                    title: "Compensation could not be Added.!",
                    details: `The credit could not be applied to the amount with error code ${result.error_code} and error message ${result.error_msg}`,
                    stepSuccessful: false
                }
                this.dialog.open(StepCompletionStatusComponent,
                    { height: '45vh', width: '40vw', data: statusData })
            }
        });
    }

    ResetPinClick() {
        let ref = this.dialog.open(ResetPinComponent,
            { height: '40vh', width: '45vw' })

        let statusData: StatusDTO = {
            title: this.status,
            details: this.statusDetails,
            stepSuccessful: this.stepSuccessful
        }

        ref.afterClosed().subscribe(result => {
            if (result == true) {
                this.dialog.open(StepCompletionStatusComponent,
                    { height: '45vh', width: '40vw', data: statusData })
            }
        })
    }



    EditNotificationPreference() {
        let ref = this.dialog.open(NotificationPreferenceComponent,
            { height: '90vh', width: '60vw' });
        ref.afterClosed().subscribe((response: boolean) => {
            if (response) {
                let status: StatusDTO = {
                    stepSuccessful: true,
                    title: 'Notification Preference Updated',
                    details: 'The notification preferences have been updated.'
                }
                this.dialog.open(StepCompletionStatusComponent, {
                    data: status,
                    height: '46vh',
                    width: '42vw'
                })
            }
        });
    }

    OnAddOrChangeSubscription(addClicked?: boolean) {
        if (this.AccountPlan) {
            const planNo = this.AccountPlan.plan_no;
            const subPackage = this.AccountPlan.rate_schedule_no;
            const masterPlanInsNo = this.AccountPlan.master_plan_instance_no;
            const svflag = addClicked ? 1 : 0;// (saveFlag) means add subscription clicked

            this.router.navigate(['CustomerPortal/change-subscription'], { queryParams: { planNo: planNo, subPackage: subPackage, svflag: svflag, mpInfo: masterPlanInsNo } });
        }
    }
}
