import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { ChangeSubscription } from './change-subscription.model';
import { ChangeSubscriptionService } from './change-subscription.service';

@Component({
  selector: 'app-change-subscription',
  templateUrl: './change-subscription.component.html',
  styleUrls: ['./change-subscription.component.scss']
})
export class ChangeSubscriptionComponent implements OnInit {

  constructor(private route: ActivatedRoute, private router: Router, private changeSubscriptionService: ChangeSubscriptionService) { }
  
  public packages : ChangeSubscription [] = [];
  public planNo : string;
  public subPackage : string;
  public masterPlanInsNo: string;
  public svflag:string;

  ngOnInit(): void {
    this.initParams();
    this.changeSubscriptionService.Get().subscribe((response)=>{
      console.log(response);
      if(response){
        this.packages = response;
      }
      
    });
  }

  initParams(){
    this.route.queryParamMap.subscribe((snap: any) => {
      if(snap){
        if ((!snap.params.planNo || snap.params.planNo && snap.params.planNo.length === 0) ||
        (!snap.params.subPackage || snap.params.subPackage && snap.params.subPackage.length === 0) ||
        (!snap.params.mpInfo || snap.params.mpInfo && snap.params.mpInfo.length === 0) ||
        (!snap.params.svflag || snap.params.svflag && snap.params.svflag.length === 0)
        ) {
        this.router.navigate(['/CustomerPortal/manage-subscription']);
      }

      this.planNo = snap.params.planNo;
      this.subPackage = snap.params.subPackage;
      this.masterPlanInsNo = snap.params.mpInfo;
      this.svflag = snap.params.svflag;
      }
    });
  }

}
