import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { Account } from 'src/app/register/register.model';
import { Ca_State_prov, Us_State_prov } from 'src/app/shared/Models/Constants';
import { StatusDTO } from 'src/app/shared/Models/Models';
import { StepCompletionStatusComponent } from 'src/app/shared/step-completion-status/step-completion-status.component';
import { PackageDetailsService } from '../package-details/package-details.service';
import { EditContactDetailsService } from './edit-contact-details.service';

@Component({
    selector: 'app-edit-contact-details',
    templateUrl: './edit-contact-details.component.html',
    styleUrls: ['./edit-contact-details.component.scss']
})
export class EditContactDetailsComponent implements OnInit {

    editContact: FormGroup;
    phoneTypes = [{ value: 'Mobile' }, { value: 'Landline' }];
    preferredLanguage = [{ displayValue: 'English', value: "System_US_English_locale" }, { displayValue: 'French', value: "French_Locale" }];
    countries = [
        { displayValue: 'United States', value: "US" },
        { displayValue: 'United Kingdom', value: "GB" },
        { displayValue: 'Canada', value: "CA" }
    ];

    stateProvDropdown : any;

    constructor(
        public dialogRef: MatDialogRef<EditContactDetailsComponent>,
        private detailService: PackageDetailsService,
        private contactService: EditContactDetailsService,
        private dialog: MatDialog,) {
    }

    ngOnInit(): void {

        this.initEditForm();

        const clientAcctId = localStorage.getItem("client_acct_id");
        const reqObj = {
            client_acct_id: clientAcctId!
        }
        this.detailService.GetAcctDetails(reqObj).subscribe((res) => {
            if (res) {
                this.editContact.setValue({
                    first_name: res.first_name,
                    last_name: res.last_name,
                    phone: res.phone,
                    cell_phone: res.cell_phone ? res.cell_phone : "",
                    address1: res.address1,
                    address2: res.address2 ? res.address2 : "",
                    city: res.city,
                    country_cd: res.country_cd ? res.country_cd : "",
                    state_prov: res.state_prov ? res.state_prov : "",
                    postal_cd: res.postal_cd,
                    email: res.email,
                    company_name: res.company_name,
                    locale_name: res.acct_locale_name ? res.acct_locale_name : ""
                });

                if(res.country_cd && res.country_cd === "US"){
                    this.stateProvDropdown = Us_State_prov;
                }else if(res.country_cd && res.country_cd === "CA"){
                    this.stateProvDropdown = Ca_State_prov;
                } else {
                    this.stateProvDropdown = [];
                }

                this.editContact.get("email")?.disable(); // disabling the email field so user can't change it.
            }
        });

    }
    
    onCountryChange(event:any){
        const selectedCountry = this.editContact.value.country_cd;
        if(selectedCountry  && selectedCountry === "US"){
            this.stateProvDropdown = Us_State_prov;
        } else if(selectedCountry && selectedCountry === "CA"){
            this.stateProvDropdown = Ca_State_prov;
        } else {
            this.stateProvDropdown = [];
        }
    }

    initEditForm() {
        this.editContact = new FormGroup({
            first_name: new FormControl('', Validators.required),
            last_name: new FormControl('', Validators.required),
            phone: new FormControl('', Validators.required),
            address1: new FormControl('', Validators.required),
            address2: new FormControl(null),
            cell_phone: new FormControl(''),
            city: new FormControl(''),
            country_cd: new FormControl('US'),
            state_prov: new FormControl(''),
            postal_cd: new FormControl(''),
            company_name: new FormControl(''),
            email: new FormControl(''),
            locale_name: new FormControl('System_US_English_locale'),
        });
    }

    onSubmit() {
        if (this.editContact.valid) {
            let reqObj;
            const data = Object.assign(this.editContact.value); 
            reqObj = data;
            const clientAcctId = localStorage.getItem("client_acct_id");
            reqObj.client_acct_id = clientAcctId;

            // hardcode condition 
            if(reqObj.country_cd === "GB"){
                reqObj.state_prov = "~";
            }
            
            this.contactService.updateContactInfo(reqObj).subscribe((res) => {
                if (res && res.error_code === 0 && res.error_msg === "OK") {

                    localStorage.setItem("first_name", data.first_name);
                    localStorage.setItem("last_name", data.last_name);

                    this.detailService.sendUserName(data); //shared service

                    let statusData: StatusDTO = {
                        title: "Contact Updated",
                        details: "Contact Information has been updated",
                        stepSuccessful: true
                    }

                    let ref = this.dialog.open(StepCompletionStatusComponent,
                        { height: '80vh', width: '40vw', data: statusData });

                    ref.afterClosed().subscribe(result => {
                        this.dialogRef.close();
                    });

                } else {
                    let statusData: StatusDTO = {
                        title: "Error occured",
                        details: res.error_msg,
                        stepSuccessful: false
                    }

                    let ref = this.dialog.open(StepCompletionStatusComponent,
                        { height: '80vh', width: '40vw', data: statusData });

                }
            });
        }
    }
}


