import { Component, OnInit, Inject } from '@angular/core';
import { FormGroup, FormControl } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';

import { AppliedFilters } from '../../../shared/Models/Models';

@Component({
  selector: 'app-advanced-filters',
  templateUrl: './advanced-filters.component.html',
  styleUrls: ['./advanced-filters.component.scss']
})
export class AdvancedFiltersComponent implements OnInit {
    dateFilter:string="Custom Range";
    hondaColor:string="#03344E";

    transactionTypeFormGroup = new FormGroup({
      "Invoice": new FormControl(false),
      "Payment": new FormControl(false)
    });

    dateRangeFormGroup = new FormGroup({
      "startDate": new FormControl(""),
      "endDate": new FormControl("")
    });

    isInvoiceChecked: boolean;
    isPaymentChecked: boolean;

  constructor(@Inject(MAT_DIALOG_DATA) public data: AppliedFilters, public matDialogRef: MatDialogRef<AdvancedFiltersComponent>) {
      data ? data.invoice ?  this.isInvoiceChecked = true : this.isInvoiceChecked = false : null;
      data ? data.payment ?  this.isPaymentChecked = true : this.isPaymentChecked = false : null;
      data? data.startDate && data.endDate ? this.dateRangeFormGroup = new FormGroup({
        "startDate": new FormControl(data.startDate),
        "endDate": new FormControl(data.endDate)
      }) : null : null
   }

  ngOnInit(): void {
  }

  onSubmit() {
    var returnObj = {
      startDate: this.dateRangeFormGroup.value.startDate,
      endDate: this.dateRangeFormGroup.value.endDate,
      payment: this.transactionTypeFormGroup.value.Payment,
      invoice: this.transactionTypeFormGroup.value.Invoice
    }

    this.matDialogRef.close(returnObj);
  }

}