import { AfterViewInit, Component, OnInit, ViewChild } from '@angular/core';
import { MatDateRangePicker } from '@angular/material/datepicker';
import { MatDialog } from '@angular/material/dialog';
import { AdvancedFiltersComponent } from '../advanced-filters/advanced-filters.component';
import { MatPaginator } from '@angular/material/paginator';
import { MatTableDataSource } from '@angular/material/table';
import { MatSort } from '@angular/material/sort';
import { Transaction_Types } from 'src/app/shared/Models/Constants';
import { PackageDetailsService } from '../../manage-subscription/package-details/package-details.service';
import { AppliedFilters } from '../../../shared/Models/Models';
import { ViewInvoiceComponent } from '../view-invoice/view-invoice.component';
import { BillingHistoryService } from './billing-history.service';

@Component({
    selector: 'app-billing-history',
    templateUrl: './billing-history.component.html',
    styleUrls: ['./billing-history.component.scss']
})
export class BillingHistoryComponent implements OnInit, AfterViewInit {
    @ViewChild('picker')
    datePicker!: MatDateRangePicker<Date>;
    dateFilter: string = "Last Month";

    displayedColumns: string[] = ['date', 'transactionType', 'amount', 'description', 'action'];
    totalData: any;
    filteredData: any;
    dataSource: any;
    appliedAdvanceFilters: AppliedFilters;

    @ViewChild(MatSort) sort: MatSort;
    @ViewChild(MatPaginator) paginator: MatPaginator;

    constructor(private matDialog: MatDialog, private detailService: PackageDetailsService, private _billingHistoryService: BillingHistoryService) {
    }


    ngAfterViewInit() {
        this.callPagerAndSort();
    }

    ngOnInit(): void {


        this.constructData();
    }

    callPagerAndSort() {
        this.dataSource = new MatTableDataSource<InvoiceHistory>(this.filteredData);
        if (this.dataSource) {
            this.dataSource.paginator = this.paginator;
            this.dataSource.sort = this.sort;
        }
    }


    constructData() {
        const clientAcctId = localStorage.getItem("client_acct_id");

        this.detailService.GetTransactionHistory({ "client_acct_id": clientAcctId }).subscribe((res) => {
            if (res && res.error_code === 0 && res.error_msg === "OK") {

                const rawData: TransHistory[] = res.history;

                const inVoiceData = rawData.filter(x => x.transaction_type === 21 || x.transaction_type === 22);
                const payment = rawData.filter(x => x.transaction_type != 21 && x.transaction_type != 22);

                var holder = {} as any;
                inVoiceData.forEach(function (d) {
                    if (holder.hasOwnProperty(d.invoice_no)) {
                        holder[d.invoice_no!] = holder[d.invoice_no!] + d.transaction_amount;
                    } else {
                        holder[d.invoice_no!] = d.transaction_amount!;
                    }
                });

                const constructInvoiceData = [];

                for (var prop in holder) {
                    const res = inVoiceData.filter(x => x.invoice_no === Number(prop));

                    constructInvoiceData.push({
                        amount: holder[prop], date: res[0].transaction_create_date,
                        description: "Invoice Charges", transactionType: "Invoice", paymentMethod: "Invoice charge line",
                        action: res[0].statement_no,
                        invoiceNo: Number(prop)
                    });
                }

                const constructedData: InvoiceHistory[] = [];
                for (const iterator of payment) {
                    let obj: InvoiceHistory = {} as InvoiceHistory;
                    obj.date = iterator.transaction_create_date;
                    obj.amount = iterator.transaction_amount;
                    obj.description = iterator.transaction_desc;

                    if (iterator.transaction_type === 2) {
                        obj.transactionType = 'Payment';
                    } else if (iterator.transaction_type === 3) {
                        obj.transactionType = 'Credit Card Payment';
                    } else {
                        const type = Transaction_Types.filter(x => x.value === iterator.transaction_type);
                        obj.transactionType = type[0].displayValue;
                    }

                    obj.action = iterator.statement_no;
                    console.log(iterator);

                    constructedData.push(obj);
                }
                constructedData.push(...constructInvoiceData);

                this.totalData = constructedData;

                // default filteration for 1 previous month
                this.filteredData = this.totalData.filter((x: InvoiceHistory) => {
                    let d1 = new Date(x.date!).getTime();
                    let lastMonth = new Date();
                    lastMonth.setDate(1);
                    lastMonth.setMonth(lastMonth.getMonth() - 1);
                    return d1 > lastMonth.getTime();
                });

                this.callPagerAndSort();
            }
        });
    }

    dateFilterClick(event: Event) {
        if ((event as any).$ngOptionLabel == "Custom Range") {
            this.datePicker.open();
        } else if ((event as any).$ngOptionLabel == "Last Month") {

            this.filteredData = this.totalData.filter((x: InvoiceHistory) => {
                let d1 = new Date(x.date!).getTime();
                let lastMonth = new Date();
                lastMonth.setDate(1);
                lastMonth.setMonth(lastMonth.getMonth() - 1);
                return d1 > lastMonth.getTime();
            });


            this.callPagerAndSort();
        } else if ((event as any).$ngOptionLabel == "Last Year") {

            this.filteredData = this.totalData.filter((x: InvoiceHistory) => {
                let current = new Date(x.date!);
                let previous = new Date(x.date!);
                previous.setFullYear(previous.getFullYear() - 1);

                return current.getTime() > previous.getTime();
            });

            this.callPagerAndSort();
        }
    }


    openAdvancedFilters() {
        let ref = this.matDialog.open(AdvancedFiltersComponent, {
            height: '72vh',
            width: '58vw',
            data: this.appliedAdvanceFilters
        });

        ref.afterClosed().subscribe(selectedAdFilters => {
            console.log(selectedAdFilters);
            if (selectedAdFilters) {
                this.appliedAdvanceFilters = selectedAdFilters;
                this.applyAdvanceFilters(selectedAdFilters);
            }

        });
    }


    applyAdvanceFilters(filters: AppliedFilters) {
        console.log(filters);
        if (filters.startDate || filters.endDate || filters.invoice || filters.payment) {
            this.filteredData = this.totalData.filter((x: InvoiceHistory) => {


                if (filters.startDate && new Date(x.date!).getTime() > new Date(filters.startDate).getTime() &&
                    filters.endDate && new Date(x.date!).getTime() < new Date(filters.endDate).getTime() &&
                    filters.invoice && x.transactionType?.toLowerCase() === "invoice" &&
                    filters.payment && x.transactionType?.toLowerCase() === "payment") {
                    return true;
                }

                if (filters.startDate && new Date(x.date!).getTime() > new Date(filters.startDate).getTime() &&
                    filters.endDate && new Date(x.date!).getTime() < new Date(filters.endDate).getTime() &&
                    filters.payment && x.transactionType?.toLowerCase() === "payment") {
                    return true;
                }

                if (filters.startDate && new Date(x.date!).getTime() > new Date(filters.startDate).getTime() &&
                    filters.endDate && new Date(x.date!).getTime() < new Date(filters.endDate).getTime() &&
                    filters.invoice && x.transactionType?.toLowerCase() === "invoice") {
                    return true;
                }

                if (filters.startDate && new Date(x.date!).getTime() > new Date(filters.startDate).getTime() &&
                    filters.endDate && new Date(x.date!).getTime() < new Date(filters.endDate).getTime() &&
                    !filters.invoice && !filters.payment) {
                    return true;
                }

                if (!filters.startDate && !filters.endDate && filters.payment && x.transactionType?.toLowerCase() === "payment") {
                    return true;
                }
                if (!filters.startDate && !filters.endDate && filters.invoice && x.transactionType?.toLowerCase() === "invoice") {
                    return true;
                }

                return false;
            });

            this.callPagerAndSort();
        }



    }

    viewInvoice(element: any, download: boolean) {
        const clientAcctId = localStorage.getItem("client_acct_id");
        const invoiceNo = element.invoiceNo;
        const reqObj = {
            client_acct_id: clientAcctId,
            invoice_no: invoiceNo
        }
        if (invoiceNo) {
            this._billingHistoryService.GetInvoiceInfo(reqObj).subscribe((res) => {
                if (res.error_code === 0 && res.error_msg === "OK" && res.out_statement && res.out_statement.length > 0) {
                    const htmlReport = res.out_statement;
                    const passObj = {
                        htmlReport: htmlReport,
                        download: download,
                        invoice_no: invoiceNo
                    }
                    let ref = this.matDialog.open(ViewInvoiceComponent, {
                        height: '72vh',
                        width: '58vw',
                        data: passObj
                    });
                }
            });
        }
    }
}


